//Categories action types
import {ICategory} from '../../Interfaces/ICategory';

import {
	CATEGORY_LOAD_START,
	CATEGORY_LOAD_ERROR,
	CATEGORY_LOAD_SUCCESS
} from '../actionTypes';


//Category action creators
export const categoryLoadStart = () => ({
	type: CATEGORY_LOAD_START
});

export const categoryLoadSuccess = (payload: Array<ICategory>) => ({
	type: CATEGORY_LOAD_SUCCESS,
	payload
});

export const categoryLoadFailure = (error: string) => ({
	type: CATEGORY_LOAD_ERROR,
	error
});
